<template>
<v-container>
    <section class="modifyuser">
        <img src="@/assets/image/OVL_logo.png" alt="">
    <!--  -->
    <div class="inputOnlyRead">
        {{name}}
    </div>
    <!--  -->
    <div class="inputBtnDiv">
        <input type="text" v-model="nickname">
        <button class="bg-freditgreen" @click="onClickNicknameValidate()"></button>
    </div>
    <!--  -->
    <div class="inputOnlyRead">
        {{email}}
    </div>
    <!--  -->
    <div>
        <input type="text" v-model="phone">
    </div>
    <!--  -->
    <div>
        <input type="password" v-model="password" style="font-size:small" placeholder="   ,     .">
    </div>
    <p class="invalidTxt" v-if="!passwordFormValid">
           8  .
    </p>
    <!--   -->
    <div>
        <input type="password" v-model="passwordCheck" style="font-size:small" placeholder=" ">
    </div>
    <p class="invalidTxt" v-if="!passwordCheckFormValid">
          .
    </p>
    <!--   -->
    <div>
        <button class="finalBtn" @click="onModifyUserBtnClick">  </button>
    </div>
    </section>
    </v-container>
</template>

<script>
import {mapGetters, mapState} from "vuex"
import axios from 'axios'
import API from '@/api/index.js'
import userAPI from '@/api/user.js'

export default {
    data() {
        return {
            nicknameValid: false,
            name: this.name,
            nickname: '',
            email: this.email,
            phone: '',
            password: '',
            passwordCheck: '',
        }
    },
    created() {
        console.log(" !! : ",this.userinfo.nickname);
        this.userid = this.userinfo.userid;
        this.name = this.userinfo.name;
        this.nickname = this.userinfo.nickname;
        this.phone = this.userinfo.phone;
        this.email = this.userinfo.email;
        
    },
    computed: {
        ...mapGetters("user", ["userinfo"]),
        ...mapState("user", ["isLogin"]),

        passwordFormValid () {
            return !this.password || ((this.password.length > 7) && /^(?=.*[a-zA-Z])(?=.*\d)(?=.*\W).{6,20}$/.test(this.password))
        },
        passwordCheckFormValid () {
            return !this.passwordCheck || (this.password===this.passwordCheck)
        },
    },
    methods: {
        onClickNicknameValidate () {
        const URL = API.url + userAPI.nickname_check(this.nickname)
            axios.get(URL)
            .then(res => {
                if (res.data === "success") {
                this.nicknameValid = true
                alert('  .')
                } else {
                alert(`${this.nickname}()   .`)
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        onModifyUserBtnClick () {
            console.log("  !");
            const URL = API.url + userAPI.modify_user()
            // const formData = new FormData()

            // formData.append('phone', this.phone)
            // formData.append('nickname', this.nickname)
            // formData.append('password', this.password)
            // formData.append const { email, name, nickname, password, phone } = this
            let pw = '';
            if(this.password.length > 0){
                pw = this.password;
            }
            var payload = {
                "userid": this.userinfo.userid,
                "nickname": this.nickname,
                "password": pw,
                "phone": this.phone,
                "name": '',
                "email": "string",
            }

            axios.put(URL, payload).then(res => {
                console.log("   : ",res.data);

                this.$store.dispatch('user/getUpdateUserInfo', this.userinfo.userid);
                this.$router.push({ name: 'Login' })
                })
                .catch(err => 
                console.error(err))
        },
    },
}
</script>

