<template>
  <!--    -->
  <v-container>
    <!--  , ,    -->
    <div class="article mt-4 mb-3">
      <div class="categoryBtn_box">
        <button @click="foodClick" :class="{'articleBtnNotSelected':!foodSelected, 'articleBtnSelected':foodSelected}"></button>
        <button @click="clothClick" :class="{'articleBtnNotSelected':!clothSelected, 'articleBtnSelected':clothSelected}"></button>
        <button @click="cosmeticClick" :class="{'articleBtnNotSelected':!cosmeticSelected, 'articleBtnSelected':cosmeticSelected}"></button>
      </div>
    </div>

    <!--   -->
    <v-dialog v-model="participation" hide-overlay max-width="300">
      <v-card>
        <!--    -->
        <v-toolbar dense color="#004627">
          <v-toolbar-title class="modalTitle">Challenge</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click="participation = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <!--    -->
        <v-container>
        <div class="modalContent">
          <div class="mb-3">
            <span class="modalContentMessage">
                 ! <br>
                    !
            </span>
          </div>
          <div class="modalContentButtonArea">
            <button class="modalContentButton" @click="participation = false"></button>
          </div>
        </div>
        </v-container>
      </v-card>
    </v-dialog>

    <!--  1:          -->
    <v-container v-if="!nowSelectedCategory.trim()">
      <v-row>
        <v-col v-for="(challenge, idx) in challengeList" :key="idx" cols="4" class="grid-cell">
          <!--  card  :   v-for      -->
          <v-container class="card">
            <article class="cardContent">
              <!-- (1)      -->
              <div class="cardContentArea">
                <img src="@/assets/image/challenge_dummy.jpg" class='cardImage'>
              </div>
              <!-- (2)  -->
              <div class="cardContentArea cardContentAreaTitle">
                {{challenge.title}}
              </div>
              <!-- (3)     -->
              <div class="cardContentArea">
                <v-icon x-small>mdi-run</v-icon>
                <span v-if="challenge.cycle === 1">
                  
                </span>
                <span v-else>
                  {{challenge.cycle}}
                </span>
              </div>
              <!-- (4)     -->
              <div class="cardContentArea">
                <v-icon x-small>mdi-diamond-stone</v-icon>
                {{challenge.score}}
              </div>
              <!-- (5)     -->
              <div class="cardContentArea">
                <v-icon x-small>mdi-account</v-icon>
                {{challenge.count}} 
              </div>
              <!-- (6)     -->
              <div class="cardContentArea">
                <v-icon x-small>mdi-calendar-blank</v-icon>
                ({{challenge.period/7}})
              </div>
              <!-- (7)   -> v-if   . -->
              <div class="cardContentArea">
                <button v-if="userinfo.challengeId.challengeId === 1" @click="participateClick(challenge.challengeId)" class="beginParticipation"></button>
                <button v-else-if="challenge.challengeId === userinfo.challengeId.challengeId" disabled class="myParticipation"></button>
                <button v-else  @click='participation = true' class="alreadyInParticipation"></button>
              </div>
            </article>
          </v-container>
        </v-col>
      </v-row>
    </v-container>

    <!--  2:        . -->
    <v-container v-else-if="nowSelectedCategory==='1'">
      <v-row>
        <v-col v-for="(challenge, idx) in foodChallengeList" :key="idx" cols="4" class="grid-cell">
          <v-container class="card">
            <article class="cardContent">
              <div class="cardContentArea">
                <img src="@/assets/image/challenge_dummy.jpg" class='cardImage'>
              </div>
              <div class="cardContentArea cardContentAreaTitle">
                {{challenge.title}}
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-run</v-icon>
                <span v-if="challenge.cycle === 1">
                  
                </span>
                <span v-else>
                  {{challenge.cycle}}
                </span>
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-diamond-stone</v-icon>
                {{challenge.score}}
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-account</v-icon>
                {{challenge.count}} 
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-calendar-blank</v-icon>
                ({{challenge.period/7}})
              </div>
              <div class="cardContentArea">
                <button v-if="userinfo.challengeId.challengeId === 1" @click="participateClick(challenge.challengeId)" class="beginParticipation"></button>
                <button v-else-if="challenge.challengeId === userinfo.challengeId.challengeId" disabled class="myParticipation"></button>
                <button v-else  @click='participation = true' class="alreadyInParticipation"></button>
              </div>
            </article>
          </v-container>
        </v-col>
      </v-row>
    </v-container>    

    <!--  3:        . -->
    <v-container v-else-if="nowSelectedCategory==='2'">
      <v-row>
        <v-col v-for="(challenge, idx) in clothChallengeList" :key="idx" cols="4" class="grid-cell">
          <v-container class="card">
            <article class="cardContent">
              <div class="cardContentArea">
                <img src="@/assets/image/challenge_dummy.jpg" class='cardImage'>
              </div>
              <div class="cardContentArea cardContentAreaTitle">
                {{challenge.title}}
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-run</v-icon>
                <span v-if="challenge.cycle === 1">
                  
                </span>
                <span v-else>
                  {{challenge.cycle}}
                </span>
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-diamond-stone</v-icon>
                {{challenge.score}}
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-account</v-icon>
                {{challenge.count}} 
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-calendar-blank</v-icon>
                ({{challenge.period/7}})
              </div>
              <div class="cardContentArea">
                <button v-if="userinfo.challengeId.challengeId === 1" @click="participateClick(challenge.challengeId)" class="beginParticipation"></button>
                <button v-else-if="challenge.challengeId === userinfo.challengeId.challengeId" disabled class="myParticipation"></button>
                <button v-else  @click='participation = true' class="alreadyInParticipation"></button>
              </div>
            </article>
          </v-container>
        </v-col>
      </v-row>
    </v-container>   

    <!--  4:        . -->
    <v-container v-else>
      <v-row>
        <v-col v-for="(challenge, idx) in cosmeticChallengeList" :key="idx" cols="4" class="grid-cell">
          <v-container class="card">
            <article class="cardContent">
              <div class="cardContentArea">
                <img src="@/assets/image/challenge_dummy.jpg" class='cardImage'>
              </div>
              <div class="cardContentArea cardContentAreaTitle">
                {{challenge.title}}
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-run</v-icon>
                <span v-if="challenge.cycle === 1">
                  
                </span>
                <span v-else>
                  {{challenge.cycle}}
                </span>
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-diamond-stone</v-icon>
                {{challenge.score}}
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-account</v-icon>
                {{challenge.count}} 
              </div>
              <div class="cardContentArea">
                <v-icon x-small>mdi-calendar-blank</v-icon>
                ({{challenge.period/7}})
              </div>
              <div class="cardContentArea">
                <button v-if="userinfo.challengeId.challengeId === 1" @click="participateClick(challenge.challengeId)" class="beginParticipation"></button>
                <button v-else-if="challenge.challengeId === userinfo.challengeId.challengeId" disabled class="myParticipation"></button>
                <button v-else  @click='participation = true' class="alreadyInParticipation"></button>
              </div>
            </article>
          </v-container>
        </v-col>
      </v-row>
    </v-container>  
  </v-container>
</template>

<script>
import { mapActions, mapGetters} from 'vuex'

export default {
  data () {
    return {
      // css  
      foodSelected: false,
      clothSelected: false,
      cosmeticSelected: false,
      //      
      nowSelectedCategory: '',
      //     
      participation: false,
    }
  },
  methods: {
    //   actions 
    ...mapActions("challenge", ["fetchChallengeList", "challengeAttend"]),

    //    
    foodClick () {
      // css  
      //      ,       .
      // food  1,  1        ?    
        if (this.nowSelectedCategory === '1') {
          this.foodSelected = false
          this.nowSelectedCategory = ''
        } else {
          //    or    ?     !
          this.foodSelected = true
          this.clothSelected = false
          this.cosmeticSelected = false
          this.nowSelectedCategory = '1'
        } 
      },
    clothClick () {
        if (this.nowSelectedCategory === '2') {
          this.clothSelected = false
          this.nowSelectedCategory = ''
        } else {
          this.foodSelected = false
          this.clothSelected = true
          this.cosmeticSelected = false
          this.nowSelectedCategory = '2'
        }
      },
    cosmeticClick () {
        if (this.nowSelectedCategory === '3') {
          this.cosmeticSelected = false
          this.nowSelectedCategory = ''
        } else {
          this.foodSelected = false
          this.clothSelected = false
          this.cosmeticSelected = true
          this.nowSelectedCategory = '3'
        }
      },
    //    
    participateClick (challenge_id) {
      if (this.userinfo.challengeId.challengeId === 1) {
        //       ?
        const payload = {
          user_id: this.userinfo.userid,
          challenge_id
        }
        this.challengeAttend(payload)
      } else {
        //  alert   .
        alert("   ")
      }
    },
  },
  computed: {
    //  store.state  4  (   )   . < v-for   >
    ...mapGetters("challenge", ["challengeList", "foodChallengeList", "clothChallengeList", "cosmeticChallengeList"]),
    // userinfo.challengeId.challengeId  1        ,     .
    ...mapGetters("user", ["userinfo",])
  },

  created () {
    // 1.           .
    this.fetchChallengeList()
  },
}
</script>

