<template>
  <v-container>
    <section class="signup">
      <img src="@/assets/image/OVL_logo.png" alt="">
      <!--  -->
      <div>
        <input type="text" placeholder="" v-model="name">
      </div>
      <!--  -->
      <div class="inputBtnDiv">
        <input type="text" placeholder="" v-model="nickname">
        <button class="bg-freditgreen" @click="onNicknameBtnClick"></button>
      </div>
      <!--  -->
      <div class="inputBtnDiv"  >
        <input type="text" placeholder="" v-model="email">
        <button class="bg-freditgreen" @click="onEmailBtnClick"></button>
      </div>
      <p class="invalidTxt" v-if="!emailFormValid">
          .
      </p>
      <!--   -->
      <div class="inputBtnDiv" v-if="emailAuthNumberSent">
        <input type="text" placeholder="" v-model="emailAuthNumber">
        <button class="bg-freditgreen" @click="onEmailAuthBtnClick"></button>
      </div>
      <!--  -->
      <div>
        <input type="text" placeholder="" v-model="phone">
      </div>
      <!--  -->
      <div>
        <input type="password" placeholder="" v-model="password">
      </div>
      <p class="invalidTxt" v-if="!passwordFormValid">
           8  .
      </p>
      <!--   -->
      <div>
        <input type="password" placeholder=" " v-model="passwordCheck">
      </div>
      <p class="invalidTxt" v-if="!passwordCheckFormValid">
          .
      </p>
      <!--   -->
      <div>
        <button class="finalBtn" :class="{ 'bg-freditgreen': signupFormValid, 'disabledBtn': !signupFormValid }" :disabled="!signupFormValid" @click="onSignupBtnClick"></button>
      </div>
    </section>
  </v-container>
</template>

<script>
// import '@/assets/css/index.scss'    .
import axios from 'axios'
import API from '@/api/index.js'
import userAPI from '@/api/user.js'
import 'url-search-params-polyfill';

export default {
  data: () => ({
    nicknameValid: false,
    emailValid: false,
    emailAuthNumberSent: false,
    emailAuthNumber: null,

    name: '',
    nickname: '',
    email: '',
    phone: '',
    password: '',
    passwordCheck: '',
  }),
  methods: {
    onNicknameBtnClick () {
    const URL = API.url + userAPI.nickname_check(this.nickname)
    axios.get(URL)
      .then(res => {
        if (res.data === "success") {
          this.nicknameValid = true
          alert('  .')
        } else {
          alert(`${this.nickname}()   .`)
        }
      })
      .catch(err => {
        console.log(err)
      })
    },
    onEmailBtnClick () {
    const URL = API.url + userAPI.email_auth('join', this.email)
    axios.get(URL)
      .then(res => {
        console.log(res)
        if (res.data === "success") {
          alert('  .')
          this.emailAuthNumberSent = true
        } else {
          alert('  .')
        }
      })
      .catch(err => console.error(err))
    },
    onEmailAuthBtnClick () {
    const URL = API.url + userAPI.email_auth_check(this.email, this.emailAuthNumber)
    axios.get(URL)
      .then(res => {
        if (res.data === "success") {
          alert('  .')
          this.emailValid = true
        } else {
          alert('  .   .')
        }
      })
      .catch(err => console.error(err))
      
    },
    onSignupBtnClick () {
      const url = API.url + userAPI.join();
      const { email, name, nickname, password, phone } = this
      const data = { email, name, nickname, password, phone }

      axios.post(url, data)
        .then((res)=>{
            if (res.data=="success") this.$router.push({name:'Login'}) 
        }).catch((err)=> {
            console.log(err);
        })
    },

  },
  computed: {
    emailFormValid () {
      return !this.email || /.+@.+\..+/.test(this.email)
    },
    passwordFormValid () {
      return !this.password || ((this.password.length > 7) && /^(?=.*[a-zA-Z])(?=.*\d)(?=.*\W).{6,20}$/.test(this.password))
    },
    passwordCheckFormValid () {
      return !this.passwordCheck || (this.password===this.passwordCheck)
    },
    signupFormValid () {
      const allExist = !!this.name && !!this.nickname && !!this.email && !!this.phone && !!this.password && !!this.passwordCheck
      const allValid = this.nicknameValid && this.emailValid  && this.emailFormValid && this.passwordFormValid && this.passwordCheckFormValid
      return allExist && allValid
    },
  }
}
</script>

